import Keyword from "../keyword/Keyword";

function Search({availble_keywords, keywords, setKeywords, search, setSearch}) {
    return (
        <div id="search">
            <input id="search-input" placeholder="Recherche…" type="text" value={search} onChange={(e) => setSearch(e.target.value.toLowerCase())} />
            <div id="keywords">
            {keywords.map((keyword, i) => (
                <Keyword
                    keyword={keyword}
                    setKeywords={setKeywords}
                    canBeDeleted
                    key={"search-" + keyword + "-" + i}
                />
            ))}
            </div>
            <div>{availble_keywords?.map((keyword, i) => (
                <Keyword setKeywords={setKeywords} keyword={keyword} key={"availble-search-" + keyword + "-" + i} />
            ))}
            </div>
        </div>
    )
}

export default Search;