import { useEffect } from "react";
import { useState } from "react";
import Article from "../article/Article";
import Button from "../button/Button";
import Form from "../form/Form";
import Window from "./Window";

function AddContent({ key, setAddContentWindow }) {
  const [status, setStatus] = useState(" ");
  const [data, setData] = useState({
    name: "Default Name",
    description:
      "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.",
    url: "url",
    img: "https://picsum.photos/350/180",
    icon: "./logo_black.svg",
    color1: "#fc8600",
    color2: "#6e4cfd",
    category: ["default keyword"],
  });

  function addContent(data) {
    setStatus("Envois en cours…");

    data.category = data.category.split(" ").map((text) => {
      if (text)
        return (
          text[0].toUpperCase() +
          text.slice(-1 * (text.length - 1)).toLowerCase()
        );
    });

    let bodydata = { fields: data };

    fetch("https://api.airtable.com/v0/appXDdr52vOVCGaXP/tblU035q0aF5L3VHO/", {
      method: "POST",
      body: JSON.stringify(bodydata),
      headers: {
        Authorization: "Bearer " + localStorage.getItem("yrl-adbk"),
        "Content-Type": "application/json",
      },
    }).then(function (res) {
      console.log(res);
      if (res.status == 200) {
        setStatus("Ajouté ! ✔✅");
      } else {
        setStatus("❌ " + res.statusText);
      }
      return;
    });
  }

  function test(e) {
    setData((obj) => {
      if (e.target.id == "category") {
        return {
          ...obj,
          [e.target.id]: e.target.value.split(" ").map((text) => {
            if (text)
              return (
                text[0].toUpperCase() +
                text.slice(-1 * (text.length - 1)).toLowerCase()
              );
          }),
        };
      } else {
        return { ...obj, [e.target.id]: e.target.value };
      }
    });
  }

  return (
    <Window
      toClose={() => {
        setAddContentWindow(false);
      }}
    >
      <Form
        action={addContent}
        submitText={"Ajouter"}
        data={[
          {
            text: "url",
            type: "text",
            require: false,
            category: "title",
            displayCategory: false,
            displayLabel: false,
            displayPlaceholder: true,
          },
          {
            text: "name",
            type: "text",
            require: true,
            category: "title",
            displayCategory: false,
            displayLabel: false,
            displayPlaceholder: true,
          },
          {
            text: "description",
            type: "textarea",
            require: false,
            category: "description",
            displayCategory: false,
            displayLabel: false,
            displayPlaceholder: true,
          },
          {
            text: "icon",
            type: "text",
            require: false,
            category: "image",
            displayCategory: false,
            displayLabel: false,
            displayPlaceholder: true,
          },
          {
            text: "img",
            type: "text",
            require: false,
            category: "image",
            displayCategory: false,
            displayLabel: false,
            displayPlaceholder: true,
          },
          {
            text: "color1",
            type: "text",
            require: false,
            category: "color",
            displayCategory: false,
            displayLabel: false,
            displayPlaceholder: true,
          },
          {
            text: "color2",
            type: "text",
            require: false,
            category: "color",
            displayCategory: false,
            displayLabel: false,
            displayPlaceholder: true,
          },
          {
            text: "category",
            type: "text",
            require: false,
            category: "keywords",
            displayCategory: false,
            displayLabel: false,
            displayPlaceholder: true,
          },
        ]}
        update={test}
      />
      <div className="preview">
        <Article data={data} />
      </div>
      <div className="status">
        <p>{status}</p>
      </div>
    </Window>
  );
}

export default AddContent;
