import { useState } from "react";
import Button from "../button/Button";
import Form from "../form/Form";
import Window from "./Window";

function Login({ setKey, setLoginWindow }) {

  const [status, setStatus] = useState("");

  function login(value) {
    console.log("setting", value.key);
    setKey(value.key);

    fetch("https://api.airtable.com/v0/appXDdr52vOVCGaXP/tblU035q0aF5L3VHO/", {
      method: "GET",
      headers: {
        Authorization: "Bearer " + value.key,
        "Content-Type": "application/json",
      },
    }).then(function (res) {
      console.log(res);
      if (res.status == 200) {
        setStatus("Connecté ! ✔✅");
      } else {
        setStatus("❌ Erreur");
      }
      return;
    });
  }

  return (
    <Window
      toClose={() => {
        setLoginWindow(false);
      }}
    >
      <Form
        data={[
          {
            type: "text",
            text: "Key",
          },
        ]}
        submitText={"Se connecter"}
        action={login}
      />
      <div>{status}</div>
    </Window>
  );
}

export default Login;
