import { useEffect, useState } from "react";
import AddContent from "./components/window/AddContent";
import Article from "./components/article/Article";
import Button from "./components/button/Button";
import Header from "./components/header/Header";
import Login from "./components/window/Login";
import Search from "./components/search/Search";
import Cat from "./components/cat/Cat";

function App() {
  let data_ = [
    {
      title: "Open Source Alternative To",
      description:
        "Discover 400+ popular free open source alternatives to mainstream SaaS products.",
      img: "https://uploads-ssl.webflow.com/6220c55c697338c9388a4715/622cb3010148d041f5b294a3_OPENSOURCE%20ALTERNATIVE.TO%20(2).png",
      url: "https://www.opensourcealternative.to/",
      category: "software opensource",
      icon: "https://uploads-ssl.webflow.com/6220c55c697338c9388a4715/622cb6248f955b9571861d0e_osa%20web%20clip.png",
    },
    {
      title: "Gradienta",
      description: "Pure CSS Code, JPG Download, Open Source!",
      img: "https://gradienta.io/static/thumbnail/thumbnail.jpg",
      url: "https://gradienta.io/",
      category: "background css opensource",
    },
    {
      title: "Gradient Hunt",
      description:
        "Gradient Hunt is a free and open platform for color inspiration with thousands of trendy hand-made color gradients with pure CSS",
      img: "https://gradienthunt.com/img/socialimage.png",
      url: "https://gradienthunt.com/",
      category: "background css opensource",
      icon: "https://gradienthunt.com/img/gradienthunt.jpg",
    },
    {
      title: "svgbackgrounds",
      description:
        "SVG Backgrounds hosts a collection of customizable SVG-based repeating patterns and backgrounds for websites and blogs.",
      img: "https://www.svgbackgrounds.com/img/svg-backgrounds-preview.png",
      url: "https://www.svgbackgrounds.com/",
      category: "background css opensource",
    },
    {
      title: "VS Code Themes",
      description: "Search and preview themes for Visual Studio Code.",
      img: "https://vscodethemes.com/thumbnail.jpg",
      url: "https://www.vscodethemes.com/",
      category: "vscode theme",
    },
    {
      title: "Happy Hues",
      description:
        "See color palette inspiration on a real example website. As you click on different palettes every color on this site updates to give you context of how that color could be used for your design or illustration projects.",
      img: "https://assets.website-files.com/5dd40aa8049df8748c72d0ee/5ddd471cbe5e6601554a2a4d_happy-hues.jpeg",
      url: "https://www.happyhues.co/",
      category: "theme",
      icon: "https://assets.website-files.com/5dd40aa8049df8748c72d0ee/5dd9b92056a2d6ed5e537c61_happy-hues-apple-icon.png",
    },
    {
      title: "Vérificateur JSON",
      description:
        "Testez votre code JSON avec le vérificateur JSON gratuit en ligne. Vous pouvez également analyser si votre code JSON est correctement structuré ou non.",
      url: "https://jsononline.net/fr/json-checker",
      category: "js",
      icon: "https://jsononline.net/themes/JsonOnline/images/JOfavicon.png",
    },
  ];
  const [loginWindow, setLoginWindow] = useState(false);
  const [AddContentWindow, setAddContentWindow] = useState(false);
  const [key, setKey] = useState(localStorage.getItem("yrl-adbk") || "");
  const [isConnected, setIsConnected] = useState(key ? true : false);
  const [data, setData] = useState([]);
  const [keywords, setKeywords] = useState([]);
  const [search, setSearch] = useState([]);

  function fetchDb(props) {
    console.log(
      "fetching " + props.database + " with " + props.offset + " ..."
    );
    let url = "";
    if (props.offset) {
      url =
        "https://api.airtable.com/v0/" +
        props.database +
        "?offset=" +
        props.offset;
    } else {
      url = "https://api.airtable.com/v0/" + props.database;
    }
    fetch(url, {
      headers: {
        Authorization: "Bearer key2mSg5mQJouk0AK",
        "Content-Type": "application/json",
      },
    })
      .then(function (res) {
        return res.json();
      })
      .then(function (obj) {
        let new_data = obj.records
          .map((item) => item.fields)
          .filter((d) => d.name);
        props.functionVar((currentArray) => currentArray.concat(new_data));
        if (obj.offset) {
          let params = {
            database: props.database,
            functionVar: props.functionVar,
            offset: obj.offset,
          };
          fetchDb(params);
        }
      });
  }

  useEffect(() => {
    if (data.length < 1) {
      console.log("> fetch data ...");
      let params = {
        database: "appXDdr52vOVCGaXP/tblU035q0aF5L3VHO/",
        functionVar: setData,
      };
      fetchDb(params);
    }
  }, []);

  useEffect(() => {
    console.log(data);
  }, [data]);

  useEffect(() => {
    fetch("https://api.airtable.com/v0/appXDdr52vOVCGaXP/tblU035q0aF5L3VHO/", {
      method: "POST",
      headers: {
        Authorization: "Bearer " + key,
        "Content-Type": "application/json",
      },
    }).then(function (res) {
      if (res.status == 422) {
        localStorage.setItem("yrl-adbk", key);
        setIsConnected(true);
        setLoginWindow(false);
      }
      return;
    });
  }, [key]);

  let voids = Array.from(
    Array(4 - (data.length % 4) +4).keys(),
    (n) => n + 1
  );
  let availble_keywords = Array.from(
    new Set(
      data
        .map((d) => d.category.join(" "))
        .join(" ")
        .split(" ")
    )
  );

  useEffect(() => {
    document.querySelectorAll("main > article").forEach((article) => {
      article.classList = "";
      if (!keywords.length > 0 && !search) return;

      let keytest = true;
      let searchtest = true;

      if (article.children[0]?.children[4]) {
        let articles_keywords = Array.from(
          article.children[0]?.children[4].childNodes
        ).map((el) => el.innerHTML);

        for (let i = 0; i < keywords.length; i++) {
          let k = keywords[i];

          if (!articles_keywords.includes(k)) {
            keytest = false;
          }
        }
      }

      if (article.children[0]?.children[3]) {
        let title =
          article.children[0]?.children[3].children[0].innerHTML.toLowerCase();
        let description =
          article.children[0]?.children[3].children[1].innerHTML.toLowerCase();

        if (!title.includes(search) && !description.includes(search)) {
          searchtest = false;
        }
      }

      if (!keytest || !searchtest) {
        article.classList = "hide";
      }
    });
  }, [keywords, search]);

  return (
    <>
      <Cat />
      <Header>
        <ul>
          <li>
            {isConnected && (
              <Button
                custom_class={["simple"]}
                text={"Ajouter une ressource"}
                action={() => setAddContentWindow((bool) => !bool)}
              />
            )}
          </li>
          <li>
            {!isConnected ? (
              <Button
                custom_class={["simple"]}
                text={"Connexion"}
                action={() => setLoginWindow((bool) => !bool)}
              />
            ) : (
              <Button
                custom_class={["simple"]}
                text={"Clé enregisté"}
                action={() => setLoginWindow((bool) => !bool)}
              />
            )}
          </li>
        </ul>
      </Header>

      <Search
        availble_keywords={availble_keywords}
        keywords={keywords}
        setKeywords={setKeywords}
        setSearch={setSearch}
        search={search}
      />

      <main>
        {data.map((d, i) => (
          <Article key={"card-" + i} setKeywords={setKeywords} data={d} />
        ))}
        {voids.map((v, i) => (
          <article key={"void-" + i} style={{ background: "none" }}></article>
        ))}
      </main>

      {loginWindow && <Login setLoginWindow={setLoginWindow} setKey={setKey} />}
      {AddContentWindow && (
        <AddContent setAddContentWindow={setAddContentWindow} />
      )}
    </>
  );
}

export default App;
