import propTypes from "prop-types";
import { useEffect, useState } from "react";

function Button(props) {
  let { text, action, icon, url, target, custom_class, disabled, submit } = props;

  if (!text && !icon) {
    text = "button";
  }

  const [classList, setClassList] = useState([]);


  useEffect(() => {

    let temp = ["button"];
    if (!text) temp.push("icon");
    if (url) temp.push("external");
    if (disabled) temp.push("disabled");
    if (custom_class) {
      temp = temp.concat(custom_class);
    } 
    setClassList(temp);

  }, [custom_class, text, url, disabled])

  function makeAction(e) {
    e.preventDefault();
    action();
  }

  return (
    <div className={classList.join(" ")}>
      {!url ? (
        <button type={submit && "submit"} disabled={disabled} onClick={!submit && makeAction}>
          {icon ? <img src={icon} /> : ""}
          {text ? <span>{text}</span> : ""}
        </button>
      ) : (
        <a disabled={disabled} href={url} target={target}>
          {icon ? <img src={icon} /> : ""}
          {text ? <span>{text} ↗</span> : ""}
        </a>
      )}
    </div>
  );
}

export default Button;

Button.propTypes = {
  text: propTypes.string,
  action: propTypes.func,
  icon: propTypes.string,
  classNames: propTypes.arrayOf(propTypes.string),
  url: propTypes.string,
  target: propTypes.oneOf(["_blank", "_self", "_parent", "_top"]),
  disabled: propTypes.bool
};

Button.defaultProps = {
  action: function() {
    console.log("clicked!")
  },
  target: "_blank",
  disabled: false
};
