import { action } from "@storybook/addon-actions";
import propTypes from "prop-types";
import { useEffect, useState } from "react";
import Button from "../button/Button";

function Form({ custom_class, id, data, submitText, action, update }) {
  data = data.map((d) => {
    return { ...d, category: d.category || null };
  });
  let categories = Array.from(new Set(data.map((d) => d.category) || []));
  const [classList, setClassList] = useState([]);

  useEffect(() => {

    let temp = ["form"];
    if (custom_class) {
      temp = temp.concat(custom_class);
    } 
    setClassList(temp);

  }, [custom_class])


  for (let i = 0; i < data.length; i++) {
    let lf = [
      "password",
      "color",
      "date",
      "datetime-local",
      "email",
      "file",
      "month",
      "number",
    ];
    let ls = ["checkbox"];
    let item = data[i];
    let id = item.text.split(" ").join("-").toLowerCase();
    if (item.id) id = item.id;
    let element;
    if (ls.includes(item.type)) {
      element = (
        <div className={"inline" + (item.require ? " require" : "")}>
          <input placeholder={item.displayPlaceholder && item.text} type={item.type} name={id} id={id} />
          {item.displayLabel && <label for={id}>{item.text}</label>}
        </div>
      );
    } else if (lf.includes(item.type)) {
      element = (
        <div className={item.require && "require"}>
          <label for={id}>{item.text ? item.text : id}</label>
          <input placeholder={item.displayPlaceholder && item.text} type={item.type} name={id} id={id} />
        </div>
      );
    } else if (item.type == "text") {
      element = (
        <div className={item.require && "require"}>
          {item.displayLabel && <label for={id}>{item.text ? item.text : id}</label>}
          <input
            list={item.datalist && id + "-list"}
            type={item.type}
            name={id}
            id={id}
            onChange={update} 
            placeholder={item.displayPlaceholder && item.text}
          />
          {item.datalist && (
            <datalist id={id + "-list"}>
              {item.datalist.map((d, i) => (
                <option key={d + "-" + i} value={d} />
              ))}
            </datalist>
          )}
        </div>
      );
    } else if (item.type == "radio") {
      element = (
        <div className={item.require && "require"}>
          {item.displayLabel && <label for={id}>{item.text ? item.text : id}</label>}
          {item.radiolist && (
            <>
              {item.radiolist.map((radio, i) => (
                <div key={radio + "-" + i} className="inline">
                  <input
                    type="radio"
                    id={radio.split(" ").join("-").toLowerCase()}
                    name={item.text.split(" ").join("-").toLowerCase()}
                  />
                  <label for={radio.split(" ").join("-").toLowerCase()}>
                    {radio}
                  </label>
                </div>
              ))}
            </>
          )}
        </div>
      );
    } else if (item.type == "textarea") {
      element = (
        <div className={item.require && "require"}>
          {item.displayLabel && <label for={id}>{item.text ? item.text : id}</label>}
          <textarea placeholder={item.displayPlaceholder && item.text} onChange={update}  name={id} id={id}></textarea>
        </div>
      );
    }

    data[i] = { ...item, element: element };
  }

  function submit(e) {
    e.preventDefault();
    let formData = new FormData(e.target);
    let data = Object.fromEntries(formData);
    action(data);
  }

  function submitButton(e) {
    e.target.parentNode.form.submit();
  }

  return (
    <div className={classList.join(" ")}>
      <form id={id} onSubmit={submit}>
        {categories.length > 0 &&
          categories.map((d, i) => (
            <fieldset key={d + "-" + i} className={"field-" + d}>
              <>
                {d && data.displayCategory && <legend>{d}</legend>}
                {data
                  .filter((b) => b.category == d)
                  .map((input) => input.element)}
              </>
            </fieldset>
          ))}
        <Button text={submitText} submit />
      </form>
    </div>
  );
}

export default Form;

Form.propTypes = {
  id: propTypes.string.isRequired,
  options: propTypes.arrayOf({
    text: propTypes.string.isRequired,
    id: propTypes.string.isRequired,
    type: propTypes.string,
    require: propTypes.bool,
    category: propTypes.string,
    datalist: propTypes.arrayOf(propTypes.string),
    radiolist: propTypes.arrayOf(propTypes.string),
  }),
  submitText: propTypes.string,
  action: propTypes.func,
};

Form.defaultProps = {
  id: "form-default-id",
  data: [{ text: "" }],
  submitText: "Submit",
  action: function () {
    /**/
  },
};
