import { logDOM } from "@testing-library/react";
import { useEffect } from "react";
import { useState } from "react";

function Cat() {
  //   console.log("RENDER");
  // let status = "idle"
  const [status, setStatus] = useState("walk");
  const [head, setHead] = useState("right");
  const [run, setRun] = useState(true);
  const [position, setPosition] = useState(false);
  const [walkDuration, setWalkDuration] = useState(0);
  const [direction, setDirection] = useState(1);

  const [incr, setIncr] = useState(0);
  const status_list = ["walk", "idle"];

  const CatObj = {
    name: "Miel",
    floor: "search-input",
  };

  // CatObj.range = document.getElementById(CatObj.floor)

  function getRandomIntInclusive(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  const walk = () => {
    setStatus("walk");
    let offset = 5;
    let r = getRandomIntInclusive(2, 4);
    // console.log("walk for " + r + "s");

    setPosition((pos) => {
      let d = getRandomIntInclusive(1, 2);
      if (d == 2) d = "-1";
      let distance = r * 10 * d * offset;

      if (
        pos + distance < 0 ||
        pos + distance > document.getElementById(CatObj.floor).offsetWidth
      ) {
        distance = distance * -1;
      }

      if (pos + distance > pos) {
        setDirection(1);
      } else {
        setDirection(-1);
      }

      return pos + distance;
    });

    setWalkDuration(r + Math.random() / 10);

    setTimeout(() => {
      setIncr((i) => i + 1);
    }, r * 1000);
  };

  useEffect(() => {
    // console.log("NEW STATUS :");

    const getNewR = () => {
      let nr = getRandomIntInclusive(1, status_list.length);
      if (status_list[nr - 1] == status) {
        return getNewR();
      } else {
        return nr;
      }
    };

    const r = getNewR();

    switch (r) {
      case 1:
        walk();
        break;
      case 2:
        idle();
        break;
    }
  }, [incr]);

  const idle = () => {
    setStatus("idle");
    setWalkDuration(0);
    let r = getRandomIntInclusive(2, 8);
    // console.log("idle for " + r + "s");
    setTimeout(() => {
      setIncr((i) => i + 1);
    }, r * 1000);
  };

  useEffect(() => {

    let CatElement = document.getElementById("cat");
    CatElement.style.top = document.getElementById(CatObj.floor).offsetTop - document.getElementById(CatObj.floor).offsetHeight  + "px";
    setWalkDuration(0);
    setPosition(document.getElementById(CatObj.floor).offsetWidth / 2);
  }, []);

  return (
    <img
      id="cat"
      src={"./cat/cat_" + status + ".gif"}
      onClick={() => setRun(!run)}
      style={{
        translate: `${position}px 0px`,
        transition: `translate ${walkDuration}s linear`,
        transform: `scaleX(${direction * -1})`,
      }}
    />
  );
}

export default Cat;
