import propTypes from "prop-types";
import Button from "../button/Button";
import Keyword from "../keyword/Keyword";

function Article({ data, setKeywords }) {
  let { name, description, url, img, icon, color1, color2, category } = data;

  function hexToRgb(hex) {
    if (!hex) return;
    var aRgbHex = hex.slice(1).match(/.{1,2}/g);
    if (!aRgbHex) return;
    let r = parseInt(aRgbHex[0], 16);
    let g = parseInt(aRgbHex[1], 16);
    let b = parseInt(aRgbHex[2], 16);
    let max = 40;
    while (r < max || g < max || b < max) {
      r++;
      g++;
      b++;
    }
    var aRgb = r + ", " + g + ", " + b;
    return aRgb;
  }

  let style = {
    background:
      "linear-gradient(to top, rgba(" +
      hexToRgb(color2) +
      ", var(--alpha)), rgba(" +
      // hexToRgb("#070510") +
      // hexToRgb("#4c4365") +
      hexToRgb(color1) +
      ", var(--alpha)))",
  };

  let shadow = {
    boxShadow: "0 0 40px 0px rgba(" + hexToRgb(color2) + ", 0.1 )",
  };

  if (!Array.isArray(category)) category = category.split(" ")

  return (
    <article style={shadow}>
      <div className="border-gradient" style={style}>
        <div className="main-gradient">
          <div style={style}></div>
        </div>

        <div className="background">
          <img src={img} alt={name} />
        </div>

        <div className="icon">
          <img src={icon ? icon : url + "/favicon.ico"} />{" "}
        </div>

        <div className="description">
          <a href={url} target="_blank">
            <h2>{name}</h2>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="44"
              height="44"
              viewBox="0 0 24 24"
              stroke-width="1.5"
              stroke="#ffffff"
              fill="none"
              stroke-linecap="round"
              stroke-linejoin="round"
            >
              <path d="M11 7h-5a2 2 0 0 0 -2 2v9a2 2 0 0 0 2 2h9a2 2 0 0 0 2 -2v-5" />
              <line x1="10" y1="14" x2="20" y2="4" />
              <polyline points="15 4 20 4 20 9" />
            </svg>
          </a>
          <p>{description}</p>
        </div>
        <div className="keywords">
          {category.map((keyword, i) => (
            <Keyword key={keyword + "-" + i} setKeywords={setKeywords} keyword={keyword} />
          ))}
        </div>
      </div>
    </article>
  );
}

export default Article;

Article.propTypes = {
  data: propTypes.exact({
    name: propTypes.string,
    description: propTypes.string,
    url: propTypes.string,
    img: propTypes.string,
    icon: propTypes.string,
    color1: propTypes.string,
    color2: propTypes.string,
    category: propTypes.string,
  }),
};

Article.defaultProps = {
  data: {
    name: "Default Name",
    description:
      "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.",
    url: "url",
    img: "https://picsum.photos/350/180",
    icon: "./logo_black.svg",
    color1: "#fc8600",
    color2: "#6e4cfd",
    category: "default keyword",
  },
};
